<template>
  <div id="home" class="container">
    <div id="search">
      <div id="search-input">
        <el-autocomplete
          :placeholder="autoCompleteOpts.placeholder"
          :prefix-icon="autoCompleteOpts.prefixIcon"
          v-model="modelName"
          :fetch-suggestions="querySearch"
          @select="handleSelectModel">
        </el-autocomplete>
      </div>
      <div id="qrcode" @click="checkQrcode">
        <img id="qrcode-image" src="../assets/images/qrcode.png">
      </div>
    </div>
    <div id="carousel">
      <el-carousel :height="carouselHeight" :interval="5000">
        <el-carousel-item v-for="(item, index) in carouselImages" :key="index">
          <img class="carousel-image" :src="item">
        </el-carousel-item>
      </el-carousel>
    </div>
    <div id="squired">
      <div class="item" v-for="(item, index) in models" :key="index">
        <img class="squired-icons" :src="item.image" @click="gotoModel(item)">
        <span class="squired-title" @click="gotoModel(item)">{{item.value}}</span>
      </div>
    </div>
    <div id="menu">
      <div id="menu-home">首页</div>
      <div id="menu-loginout" @click="loginout">登出</div>
    </div>
  </div>
</template>
<script>

export default {
  name: 'home',
  data () {
    return {
      carouselImages: [
        require('../assets/images/home-image1.png'),
        require('../assets/images/home-image2.png')
      ],
      modelName: '',
      models: [],
      autoCompleteOpts: {
        prefixIcon: 'el-icon-search',
        placeholder: '请输入要查看的功能'
      },
      carouselHeight: ''
    }
  },
  mounted () {
    this.carouselHeight = (window.screen.availWidth - 10) / 2.1 + 'px'
    this.models = this.loadAll()
  },
  methods: {
    loadAll () {
      return [
        { value: '阿姨管理', name: 'Aunt', image: require('../assets/images/manageAyi.png') },
        { value: '需求管理', name: '需求列表', image: require('../assets/images/manageNeeds.png') },
        { value: '订单管理', name: '订单管理', image: require('../assets/images/manageOrder.png') },
        { value: '基本信息', name: '基本信息', image: require('../assets/images/basicInfo.png') },
        { value: '版本管理', name: '版本管理', image: require('../assets/images/manageVersion.png') },
        { value: '修改密码', name: '修改密码', image: require('../assets/images/modifyPassword.png') },
        { value: '账号管理', name: '账号管理', image: require('../assets/images/manageAccount.png') },
        { value: '', name: '' },
        { value: '', name: '' }
      ]
    },
    querySearch (queryString, cb) {
      let results = queryString ? this.createFilter(queryString, this.models) : this.models
      cb(results)
    },
    createFilter (queryString, models) {
      return models.filter((model) => {
        return (model.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0)
      })
    },
    handleSelectModel (item) {
      this.modelName = item.value
      this.$router.push({ name: `${item.name}` })
    },
    gotoModel (item) {
      this.$router.push({ name: `${item.name}` })
    },
    loginout () {
      sessionStorage.clear()
      this.$router.replace('/')
    },
    checkQrcode () {
      // console.log(Object.getPrototypeOf(this))
      this.$notify({
        title: '提示信息',
        message: '该功能暂未上线',
        position: 'bottom-right',
        offset: 120
      })
    }
  }
}
</script>
<style scoped>
.container {
  height: 100%;
  background: #F7F7F7;
}
.carousel-image {
  width: 100%;
  height: 100%;
}
#search {
  position: fixed;
  top: 0;
  background: #5D90CF;
  width: 100%;
  height: 60px;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
#search-input {
  width: 80%;
}
.el-autocomplete {
  width: 100%;
}
#qrcode-image {
  height: 35px;
  margin-right: 6px;
}
#carousel {
  margin: 65px 5px 10px 5px;
}
#menu {
  width: 100%;
  height: 48px;
  position: fixed;
  bottom: 0px;
  background: white;
  display: flex;
}
#menu-home {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #5D90CF;
  font-size: 16px;
  font-weight: bold;
}
#menu-loginout {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  font-weight: bold;
}
#squired {
  margin-top: 50px;
  margin: 0 auto;
  width: 95%;
  display: grid;
  grid-template-columns: 33.5% 33.5% 33.5%;
  /* grid-template-rows: 120px 120px 120px; */
  box-shadow: 5px 5px 10px #D4D4D4;
  margin-bottom: 70px;
}
.item {
  background-color: white;
  margin: 1px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 16px;
  padding-top: 30px;
  padding-bottom: 30px;
}
.item img {
  width: 40px;
  margin-bottom: 10px;
}
</style>



